body{
    color: white;
    font-family: Maximum Impact;
}

/* style quand la scrollbar ne va pas bouger*/
::-webkit-scrollbar{
    background: transparent;
}

/* style quand la scrollbar va pas bouger à l'horizontal */
::-webkit-scrollbar-thumb:horizontal{
    background-color: rgba(0,0,0,0.61);
    border-radius: 10px;
}